import React from 'react'
import styled from 'react-emotion'

const Container = styled('button')`
  position: fixed;
  right: 0;
  bottom: 0;
  margin: 0 2rem 2rem 0;
  background-color: var(--primary);
  color: var(--background);
  font-size: 2rem;
  border-radius: 1000px;
  padding: 1rem;
  border: 0;
  box-shadow: var(--shadow-base);
  outline: none;
  transition: 1s ease-out;
  &:hover{
    cursor: pointer;
    color: var(--primary);
    background-color: var(--background);
    box-shadow: var(--shadow-hover);
  }
`

const NewRecipeButton = ({isActive,toggleNewRecipe})=>{
  return(
    <Container onClick={toggleNewRecipe}>
    {
      (isActive == false) ? (
      <svg width="32px" height="32px" viewBox="0 0 32 32" version="1.1" xmlns="http://www.w3.org/2000/svg" >
        <g id="Page-1" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd" strokeLinecap="round" strokeLinejoin="round">
              <path d="M16,16 L16,4 L16,16 L28,16 L16,16 Z M16,16 L16,28 L16,16 L4,16 L16,16 Z" id="Combined-Shape" stroke="#DEE3E7" strokeWidth="7"></path>
        </g>
      </svg>)
      :
      (<svg width="32px" height="7px" viewBox="0 0 32 7" version="1.1" xmlns="http://www.w3.org/2000/svg">
        <g id="Page-1" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd" strokeLinecap="round" strokeLinejoin="round">
              <path d="M15.9739046,-8.5 L15.9739046,15.5" id="Path-2-Copy-2" stroke="#DEE3E7" strokeWidth="7" transform="translate(15.973905, 3.500000) rotate(90.000000) translate(-15.973905, -3.500000) "></path>
        </g>
      </svg>)
    }
    </ Container>
  )
}

export default NewRecipeButton
